import * as React from 'react';
import PropTypes from 'prop-types';
import TextField from '@mui/material/TextField';
import Autocomplete, { autocompleteClasses } from '@mui/material/Autocomplete';
import useMediaQuery from '@mui/material/useMediaQuery';
import ListSubheader from '@mui/material/ListSubheader';
import Popper from '@mui/material/Popper';
import { useTheme, styled } from '@mui/material/styles';
import { VariableSizeList } from 'react-window';
import Typography from '@mui/material/Typography';

const LISTBOX_PADDING = 8; // px

function renderRow(props) {
  const { data, index, style } = props;
  const dataSet = data[index];
  const inlineStyle = {
    ...style,
    top: style.top + LISTBOX_PADDING,
  };

  if (dataSet.hasOwnProperty('group')) {
    return (
      <ListSubheader key={dataSet.key} component="div" style={inlineStyle}>
        {dataSet.group}
      </ListSubheader>
    );
  }

  return (
    <Typography component="li" {...dataSet[0]} noWrap style={inlineStyle}>
      {dataSet[1]}
    </Typography>
  );
}

const OuterElementContext = React.createContext({});

const OuterElementType = React.forwardRef((props, ref) => {
  const outerProps = React.useContext(OuterElementContext);
  return <div ref={ref} {...props} {...outerProps} />;
});

function useResetCache(data) {
  const ref = React.useRef(null);
  React.useEffect(() => {
    if (ref.current != null) {
      ref.current.resetAfterIndex(0, true);
    }
  }, [data]);
  return ref;
}

// Adapter for react-window
const ListboxComponent = React.forwardRef(function ListboxComponent(props, ref) {
  const { children, ...other } = props;
  const itemData = [];
  children.forEach((item) => {
    itemData.push(item);
    itemData.push(...(item.children || []));
  });

  const theme = useTheme();
  const smUp = useMediaQuery(theme.breakpoints.up('sm'), {
    noSsr: true,
  });

  const itemCount = itemData.length;
  const itemSize = smUp ? 36 : 48;

  const getChildSize = (child) => {
    if (child.hasOwnProperty('group')) {
      return 48;
    }

    return itemSize;
  };

  const getHeight = () => {
    if (itemCount > 8) {
      return 8 * itemSize;
    }
    return itemData.map(getChildSize).reduce((a, b) => a + b, 0);
  };

  const gridRef = useResetCache(itemCount);

  return (
    <div ref={ref}>
      <OuterElementContext.Provider value={other}>
        <VariableSizeList
          itemData={itemData}
          height={getHeight() + 2 * LISTBOX_PADDING}
          width="100%"
          ref={gridRef}
          outerElementType={OuterElementType}
          innerElementType="ul"
          itemSize={(index) => getChildSize(itemData[index])}
          overscanCount={5}
          itemCount={itemCount}
        >
          {renderRow}
        </VariableSizeList>
      </OuterElementContext.Provider>
    </div>
  );
});

ListboxComponent.propTypes = {
  children: PropTypes.node,
};

function random(length) {
  const characters =
    'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  let result = '';

  for (let i = 0; i < length; i += 1) {
    result += characters.charAt(Math.floor(Math.random() * characters.length));
  }

  return result;
}

const StyledPopper = styled(Popper)({
  [`& .${autocompleteClasses.listbox}`]: {
    boxSizing: 'border-box',
    '& ul': {
      padding: 0,
      margin: 0,
    },
  },
});

export default function Virtualize({ model, params, value, valueKey, getOptionLabel, onChange, name, onInputChange}) {
    const [inputValue, setInputValue] = React.useState('');
    const [internalValue, setInternalValue] = React.useState(null);

    const { data: internalOptions } = model.useModel({
        ...params,
        size: 9999
      });
    
      React.useEffect(() => {
        if (value && internalOptions?.rows) {
          internalOptions?.rows.map((item) => {
            if (item[valueKey] === value) {
              setInputValue(item[getOptionLabel]);
              setInternalValue(item[getOptionLabel]);
            }
            return null;
          });
        }
        // eslint-disable-next-line
      }, [value, internalOptions?.rows]);

  return (
    <Autocomplete
        fullWidth
        id="virtualize-demo"
        disableListWrap
        PopperComponent={StyledPopper}
        ListboxComponent={ListboxComponent}
        value={internalValue}
        inputValue={inputValue}
        onChange={(event, value) => {
            setInternalValue(value ? value[getOptionLabel] : null);
            // if (array) onChange(array, value ? value : null, true);
            return onChange
            ? onChange(
                {
                    target: {
                    name: name,
                    value: value ? value[valueKey] : null,
                    },
                },
                value
                )
            : null;
            // return onChange ? onChange(name,, true) : null;
        }}
        onInputChange={(event, newInputValue) => {
            setInputValue(newInputValue);
            if (onInputChange) {
            onInputChange(event, newInputValue);
            }
        }}
        options={internalOptions?.rows}
        renderInput={(params) => <TextField {...params} variant="standard" label="10,000 options" />}
        // renderOption={(props, option) => [props, option]}
        getOptionLabel={(option) =>
            option === inputValue ? inputValue : option[getOptionLabel]
          }
        renderOption={(props, option) => [props, option[getOptionLabel]]}
    />
  );
}